<template>
<div class="diary">
 <header>
        我的心情日记
    </header>
    <div v-for="(article, index) in lists" :key="index" >

        <div class="month">{{this.month[new Date(this.lists[index][1]*1000).getMonth()]}} {{new Date(this.lists[index][1]*1000).getFullYear()}} ( {{this.lists.length}} entries )</div>
            <div class="warp">
                <div class="date">
                  <span>{{new Date(this.lists[index][1]*1000).getDate()}}</span>{{new Date(this.lists[index][1]*1000).getFullYear()}}/{{this.month[new Date(this.lists[index][1]*1000).getMonth()]}}
                </div>
                <div class="card">
                <span v-html="article[2]"></span>
                    <div class="time">{{new Date(this.lists[index][1]*1000).getHours()}}:{{new Date(this.lists[index][1]*1000).getMinutes()}}
                    </div>
                </div>
            </div>
       </div>
    </div>
</template>

<script>
export default {
  name: 'MyDiary',
  props: {},
  data(){
    return {
      lists:[],
      month:["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Set","Oct","Nov","Dec"]
    }
  },
  mounted(){
    this.axios.get('https://codingboy.tk/diary/fetch_all.php')
    .then(res=>{
    this.lists=res.data;
    })
  }
}
</script>

